html {
  -webkit-text-size-adjust: 100%;
}

html, body {
  background-repeat: no-repeat;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  height: 100%;
}

*, *:before, *:after {
  font-family: var(--font-lato);
  outline: none;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, em, img, strong, sub, sup, b, u, i, dl, dt, dd, ol, ul, li, fieldset, form, label, table, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video, input, textarea, blockquote {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  list-style: none;
}

body {
  overflow-x: hidden;
  font-size: 14px;
  line-height: 16px;
  color: var(--main-text-color);
}

#__next {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100%;
  min-height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

button {
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
}
button[disabled] {
  cursor: default;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, td img {
  vertical-align: top;
}

input, select, button, textarea {
  margin: 0;
  outline: none;
  border: 0;
}

input[type=checkbox] {
  vertical-align: bottom;
}

input[type=radio] {
  width: auto;
}

input[type=text], input[type=email], input[type=phone] {
  text-overflow: ellipsis;
}

input[type=number] {
  -moz-appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number]:hover, input[type=number]:focus {
  -moz-appearance: number-input;
}

sub {
  vertical-align: sub;
  font-size: smaller;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

::selection {
  background-color: var(--main-color);
  color: #fff;
}

.gl-layout-content, .gl-layout-wrapper {
  flex: 1 1 100%;
  display: flex;
  flex-direction: column;
}

