.header__btn-burger-wrapper {
  width: 56px;
  height: 56px;
  margin: calc((56px - 42px) / 2 * -1) calc((56px - 42px) / 2 * -1) calc((56px - 42px) / 2 * -1) calc(calc((56px - 42px) / 2 * -1) + 80px);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
@media screen and (max-width: 759px) {
  .header__btn-burger-wrapper {
    margin-left: calc(calc((56px - 42px) / 2 * -1) + 20px);
  }
}
.header__btn-burger {
  /* средняя линия бургера */
  display: block;
  width: 42px;
  height: 4px;
  background-color: var(--main-color);
  border-radius: 4px;
  /* верхняя и нижняя линии бургера */
}
.header__btn-burger:before, .header__btn-burger:after {
  content: "";
  position: absolute;
  transition: all var(--transition-duration-short) ease;
  background-color: var(--main-color);
  border-radius: 4px;
  height: 4px;
}
.header__btn-burger:before {
  top: calc((56px -
        4px * 3 -
        8px * 2) / 2);
  width: 42px;
  left: 50%;
  transform: translateX(-50%);
}
.header__btn-burger:after {
  width: 32px;
  bottom: calc((56px -
        4px * 3 -
        8px * 2) / 2);
  right: 50%;
  transform: translateX(calc(50% + (42px - 32px) / 2));
}

.header__select-wrapper {
  width: auto;
  min-width: 120px;
}
.header__select-wrapper .ss-main {
  position: relative;
  display: inline-block;
  user-select: none;
  width: 100%;
}
.header__select-wrapper .ss-main .ss-single-selected {
  display: flex;
  cursor: pointer;
  width: 100%;
  height: 36px;
  padding: 6px;
  border-radius: 4px;
  background-color: #ffffff;
  outline: 0;
  box-sizing: border-box;
  transition: background-color 0.2s;
}
.header__select-wrapper .ss-main .ss-single-selected.ss-disabled {
  background-color: #dcdee2;
  cursor: not-allowed;
}
.header__select-wrapper .ss-main .ss-single-selected.ss-open-above {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.header__select-wrapper .ss-main .ss-single-selected.ss-open-below {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.header__select-wrapper .ss-main .ss-single-selected .placeholder {
  display: flex;
  flex: 1 1 100%;
  align-items: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: left;
  width: calc(100% - 30px);
  line-height: 1em;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.header__select-wrapper .ss-main .ss-single-selected .placeholder * {
  display: flex;
  align-items: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: auto;
}
.header__select-wrapper .ss-main .ss-single-selected .placeholder .ss-disabled {
  color: #dedede;
}
.header__select-wrapper .ss-main .ss-single-selected .ss-deselect {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0 1 auto;
  margin: 0 6px 0 6px;
  font-weight: bold;
}
.header__select-wrapper .ss-main .ss-single-selected .ss-deselect.ss-hide {
  display: none;
}
.header__select-wrapper .ss-main .ss-single-selected .ss-arrow {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0 1 auto;
  margin: 0 6px 0 6px;
}
.header__select-wrapper .ss-main .ss-single-selected .ss-arrow span {
  border: 5px solid transparent;
  border-top: 6px solid var(--main-text-color);
  display: inline-block;
  transition: transform 50ms;
}
.header__select-wrapper .ss-main .ss-single-selected .ss-arrow span.arrow-up {
  transform: translateY(-3px) rotate(-180deg);
}
.header__select-wrapper .ss-main .ss-single-selected .ss-arrow span.arrow-down {
  transform: translateY(3px);
}
.header__select-wrapper .ss-main .ss-multi-selected {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  min-height: 36px;
  width: 100%;
  padding: 0 0 0 3px;
  border: 1px solid #dcdee2;
  border-radius: 4px;
  background-color: #ffffff;
  outline: 0;
  box-sizing: border-box;
  transition: background-color 0.2s;
}
