  width: 42px;
  left: 50%;
  transform: translateX(-50%);
}
.header__btn-burger:after {
  width: 32px;
  bottom: calc((56px -
        4px * 3 -
        8px * 2) / 2);
  right: 50%;
  transform: translateX(calc(50% + (42px - 32px) / 2));
}

